<template>
<!-- <el-progress class="pros" type="circle" :percentage="20">  </el-progress> -->
  <div id="WebGL" style="width: 100%; height: 100%"></div>
</template>



<script>
import { onBeforeUnmount, onMounted } from "@vue/runtime-core";
import VueScene from "../script/View3D";
import { ElLoading } from "element-plus";
import TWEEN from "tween";
export default {
  setup() {



    let airScene;
    let loadingInstance;
    let skycubes;
    let mixer;
    let clock;
    let mixerUpdateID;
    let timeID;
    onMounted(() => {
      loadingInstance = ElLoading.service({ fullscreen: true });
      airScene = new VueScene("WebGL");
    //   airScene.axesHelper();
      //   airScene.ambientLight.intensity = 0.5;
      lights();

      clock = airScene.Clocks();

      ModelLoading().then(() => {
        loadingInstance.close();
      });

      mixerUpdate();
      evenlisten();
    });

    onBeforeUnmount(() => {
      airScene.SceneDestory();
      cancelAnimationFrame(mixerUpdateID);
      console.log("场景销毁了");
    });

    const lights = () => {
      const spotlight = airScene.addSpotlight(0xffffff, 2);
      spotlight.position.set(-10, 10, 0);
      //   const helper = airScene.spotHelper(spotlight);
      //   airScene.scene.add(helper);
      airScene.scene.add(spotlight);
      skycubes = airScene.loadSkyBox("./skycube1/");
      airScene.scene.background = skycubes;
    };

    const ModelLoading = async () => {
      const airplanes = await airScene.LoadModel("./resources/airs.glb");

      airScene.scene.add(airplanes.scene);
      airplanes.scene.traverse((child) => {
        if (child.isMesh) {
          child.material.envMap = skycubes;
          child.material.envMapIntensity = 2;
        }
      });

      mixer = airScene.GetMixer(airplanes.scene);
      mixer.timeScale = 0.0;
      mixer.clipAction(airplanes.animations[0]).play();
      window.mixer = mixer;
    };

    const mixerUpdate = () => {
      mixerUpdateID = requestAnimationFrame(mixerUpdate);
      const delta = clock.getDelta();
      if (mixer) {
        mixer.update(delta);
      }
      if (TWEEN) {
        TWEEN.update();
      }
    };

    //键盘事件监听
    const evenlisten = () => {
      window.addEventListener("keydown", (event) => {
        // console.log(event);
        if (event.key == "w") {
          //   console.log("前进");

          if (mixer.timeScale <= 1) {
            mixer.timeScale += 0.01;
          }
        }
        if (timeID) {
          clearTimeout(timeID);
        }
        timeID = setTimeout(() => {
          // console.log("松手了");
          cartoons();
        }, 1000);        
      });
    };

    const cartoons = () => {
      const values = { x: mixer.timeScale };

      console.log(values.x);

      const stops = new TWEEN.Tween(values);
      stops.to({ x: 0 }, 2000);
      stops.onUpdate(() => {
        mixer.timeScale = values.x;
        // console.log();
      });
      stops.start();
    };
  },
};
</script>